.pcities {
	@include md {
		display: none; }

	&__title {
		margin-bottom: 15px; }
	&__input {
		width: 300px;
		margin-bottom: 20px;
		@include sm {
			width: 100%; } }
	&__items {
		column-count: 5;
		column-gap: 40px;
		@include md {
			column-count: 3;
			column-gap: 25px; }
		@include sm {
			column-count: 1;
			column-gap: 0; } }
	&__item {
		margin-bottom: 25px;
		display: inline-block;
		width: 100%;
		overflow: hidden;
		break-inside: avoid-column;
		-webkit-column-break-inside: avoid;
		.item {
			display: flex;
			align-items: flex-start;
			&__letter {
				@include PBold;
				font-size: 20px;
				margin-right: 10px; }
			&__list {
				text-align: left;
				li {
					font-size: 14px;
					a {
						color: $cyan; } } } } } }


.mcities {
	text-align: center;
	padding-top: 6px;
	display: none;
	@include md {
		display: block; }

	&__input {
		margin-bottom: 20px;
		position: relative;
		.icon {
			position: absolute;
			top: 50%;
			left: 20px;
			margin-top: -6px;
			font-size: 12px;
			fill: $grey; } }
	&__title {
		@include PSemi;
		font-size: 20px;
		color: $black;
		margin-bottom: 15px; }
	&__list {
		text-align: left;
		margin-bottom: 20px; }
	&__item {
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&__link {
		display: block;
		border: 1px solid $cyan;
		border-radius: 25px;
		color: $cyan;
		font-size: 16px;
		min-height: 40px;
		background-color: #edf9fb;
		padding-left: 21px;
		position: relative;
		padding-top: 12px;
		padding-right: 30px;
		padding-bottom: 5px;
		@include PReg;
		&:before {
			content: '';
			position: absolute;
			right: 22px;
			top: 50%;
			margin-top: -4px;
			height: 9px;
			width: 9px;
			border-left: 2px solid $cyan;
			border-bottom: 2px solid $cyan;
			transform: rotate(-135deg); } } }
