.doctor-page {
	background-color: $almost-white;
	&__block {
		background-color: $white;
		padding-top: 100px;
		@include md {
			padding-top: 50px; } }
	&__title {
		margin-bottom: 10px;
		@include md {
			text-align: center;
			font-size: 25px; } }
	&__textual {
		margin-bottom: 35px;
		@include md {
			text-align: center;
			margin-bottom: 20px; } }
	&__clinic-boxes {
		padding: 100px 0;
		@include md {
			padding: 50px 0; } }
	&__about-doctor {
		margin-bottom: 100px;
		@include md {
			margin-bottom: 45px; } }
	&__work-place {
		margin-bottom: 45px; }
	.clinic-boxes {
		padding-bottom: 50px; }
	.filters {
		margin-bottom: 30px; }
	.costs {
		margin-bottom: 10px;
		@include md {
			margin-left: -40px;
			width: calc(100% + 80px);
			margin-bottom: 25px; }
		@include sm {
			margin-left: -40px;
			width: calc(100% + 80px); }
		&-table {
			&__col_indent_md {
				@include md {
					width: 35%;
					min-width: 35%; } }
			&__col_indent_sm {
				@include md {
					width: 65%; }
				.costs-table__item {
					@include md {
						width: 50%; } } }
			&__title {
				&_secondary {
						@include md {
							display: none; } }
				&_tablet {
					display: none;
					@include md {
						display: block; } } } } }

	.section-sales {
		margin: 100px 0 100px;
		&__title {
			font-size: 25px;
			margin-bottom: 30px; }
		@include md {
			margin: 50px 0; } }
	.count-slider {
		margin-bottom: 0; }
	.rate-stars {
		margin-left: 0; }
	.cslide__recomend {
		margin-top: 10px; }
	.filters__field {
		@include md {
			padding: 0; } }
	.clinic-box__content {
		.clinic-box__titlelink, .clinic-box__tablelist {
			@include md {
				display: none; } } }
	.clinic-box__map {
		@include md {
			margin-bottom: 0; } } }
