.clinic-block {
	@include md {
		padding: 20px; }
	&__ratings {
		@include clr;
		padding-bottom: 13px;
		border-bottom: 1px solid #e7eced;
		margin-bottom: 16px;
		@include md {
			padding-bottom: 0;
			border-bottom: none; } }
	&__title {
		margin-bottom: 7px;
		@include md {
			font-size: 16px; }
		@include r(399) {
			font-size: 14px; } }
	&__list {
		margin-top: 4px;
		margin-bottom: 8px; }

	.count-users {
		float: left;
		@include md {
			float: none; } }
	.rate-wrapper {
		float: right;
		@include md {
			float: none;
			padding-bottom: 11px;
			border-bottom: 1px solid #e7eced;
			margin-bottom: 11px; } }
	.rate-number {
		margin-left: 20px; }
	.rate-stars {
		padding-top: 4px; }
	.list {
		margin-top: 0;
		margin-bottom: 0;
		@include md {
			padding-bottom: 10px;
			border-bottom: 1px solid #e7eced; } }
	.box-general__right-map {
		border-radius: 6px;
		margin-bottom: 20px;
		@include md {
			margin-bottom: 8px; } }
	.box-general__right-info {
		@include md {
			padding-bottom: 2px;
			border-bottom: 1px solid #e7eced;
			margin-bottom: 18px; } }
	.location-info, .metro-info, .worktime-info {
		font-size: 16px;
		@include md {
			font-size: 13px; } }
	.location-info {
		a {
			@include md {
				border-bottom: 1px dashed $black; } } }
	.metro-info {
		.icon {
			top: -1px;
			@include md {
				top: -3px; } } }
	.map-link {
		@include md {
			text-align: center; }
		img {
			@include md {
				max-width: 100%; } } }
	.button {
		@include xs {
			display: block;
			text-align: center; } } }
