.textfield {
	background-color: transparent;
	border: 1px solid $grey;
	border-radius: 20px;
	padding: 12px 16px 10px;
	position: relative;
	@include PReg;
	font-size: 14px;
	color: $black;
	width: 100%;
	@include placeholder {
		color: $grey; }
	&_mod {
		padding-left: 37px;
		letter-spacing: -.6px; } }

.input {
	appearance: none;
	font-size: 16px;
	color: $black;
	width: 100%;
	box-shadow: 0 1px 9px rgba(67, 158, 167, 0.47);
	border-radius: 25px;
	padding: 19px 22px 12px;
	background-color: #fff;
	@include PReg;
	&:focus::-webkit-textfield-decoration-container {
		visibility: hidden;
		pointer-events: none;
		position: absolute;
		left: 20px; }
	@include placeholder {
		color: $grey; }
	&--transparent {
		cursor: pointer;
		font-size: 14px;
		color: $white;
		background-color: transparent;
		@include placeholder-color($white);
		border: 1px solid $white;
		box-shadow: none; }
	&--icon {
		padding-right: 30px; } }

.field {
	position: relative; }

.input-icon {
	position: absolute;
	top: 50%;
	right: 26px;
	margin-top: -9px;
	.icon {
		stroke: #c8c8c8;
		height: 12px;
		width: 18px;
		stroke-width: 2px; } }


.form-field {
	display: flex;
	align-items: center;
	padding-bottom: 20px;
	@include sm {
		flex-direction: column;
		align-items: flex-start; }
	label {
		@include PReg;
		font-size: 16px;
		color: $black;
		width: 120px;
		word-break: break-word;
		@include sm {
			font-size: 14px;
			margin-left: 16px; } }
	select {
		display: none; }
	&__in {
		flex-grow: 1;
		position: relative;
		@include sm {
			width: 100%; }
		&_flex {
			display: flex;
			justify-content: space-between; }
		&.is-invalid {
			.textfield, .custom-select {
				border-color: $pink; } } }
	&__col {
		width: percentage(130/280);
		position: relative;
		&.is-invalid {
			.textfield, .custom-select {
				border-color: $pink; } } }
	&__error {
		color: $pink;
		font-size: 12px;
		position: absolute;
		width: 100%;
		top: 100%;
		left: 0;
		display: block;
		text-align: center; }
	.textfield {
		padding-left: 20px;
		padding-right: 20px;
		&:focus {
			border-color: $cyan; } }
	.custom-select {
		border: 1px solid $grey;
		border-radius: 20px;
		font-size: 14px;
		&.is-open {
			border-color: $cyan;
			.custom-select__dropdown {
				border: 1px solid $cyan;
				border-top-color: $grey;
				width: calc(100% + 2px);
				left: -1px; } }
		&__field {
			padding: 12px 16px 8px;
			margin-left: 4px;
			margin-right: 22px; }
		&__dropdown {
			color: $black;
			padding-top: 5px;
			padding-bottom: 0; } } }

.input-autocomplete {
	position: relative;
	width: 100%;
	&.is-active {
		input {
			border-radius: 20px 20px 0 0; }
		.input-autocomplete {
			&__dropdown {
				display: block; } } }
	input {
		margin-right: 0;
		padding: 11px 20px 14px;
		width: 100%; }
	&__dropdown {
		background-color: $white;
		border-radius: 0 0 20px 20px;
		box-shadow: 0 4px 10px rgba(67,158,167,.18);
		display: none;
		font-size: 13px;
		top: 100%;
		left: 0;
		padding: 0 20px 20px;
		position: absolute;
		max-height: 130px;
		overflow: hidden;
		width: 100%;
		&.is-empty {
			.input-autocomplete {
				&__search-empty {
					display: block; } } } }
	&__value {
		cursor: pointer;
		&:hover {
			color: $cyan; }
		&.is-hidden {
			display: none; } }
	&__search-empty {
		display: none; } }
