.location-info, .metro-info, .worktime-info {
	padding-left: 20px;
	position: relative;
	line-height: 1.2;
	font-size: 13px;
	margin-bottom: 8px;
	display: block; }
.location-info {
	&__icon {
		position: absolute;
		top: 2px;
		left: 3px;
		.icon {
			fill: $cyan;
			display: block; } }
	a {
		color: $black;
		text-decoration: none;
		@include trans;
		&:hover {
			opacity: .7; } }
	&--dashed {
		text-decoration: underline;
		text-decoration-style: dashed; } }
.metro-info {
	color: $black;
	.icon {
		position: absolute;
		top: -3px;
		left: -3px;
		fill: $cyan;
		font-size: 21px; }
	a {
		color: $cyan;
		// span
		// 	&:not(.gray-color)
 }		// 		color: $black
	.gray-color {
		color: #cacdce; } }
.worktime-info {
	&:before {
		content: "";
		@include s('worktime-icon');
		display: block;
		width: 14px;
		height: 14px;
		position: absolute;
		top: 0px;
		left: 0; } }



