.callback {
	background-color: #19c4e7;
	padding-top: 47px;
	padding-bottom: 48px;
	text-align: center;
	@include md {
		padding-top: 40px; }
	&__title {
		margin-bottom: 41px;
		@include md {
			font-size: 20px;
			margin-bottom: 21px; } }
	&__list {
		margin: 0 0px 30px;
		padding: 0 50px;
		@include clr;
		@include r(1260) {
			padding: 0;
			display: flex;
			justify-content: center;
			margin: 0 -40px 30px; }
		@include md {
			width: 100%;
			padding: 0;
			display: block;
			margin: 0 0 9px; } }
	&__item {
		float: left;
		padding: 0 10px;
		position: relative;
		@include r(1110) {
			padding: 0 5px; }
		@include md {
			padding: 0 10px;
			&:not(:last-child) {
				margin-bottom: 15px; } }
		.input {
			padding: 17px 22px 14px; }

		&.date {
			.custom-select {
				&__field {
					padding: 17px 22px 14px; } } } }
	&__form {
		@include clr; }
	&__picker {
		width: 190px;
		@include r(1110) {
			width: 150px; }
		@include md {
			width: 50%; } }
	&__textfield {
		width: 240px;
		@include md {
			width: 100%; } }
	&__submit {
		width: 220px;
		@include md {
			width: 100%; } }
	&__btn {
		background-color: $green;
		color: #fff;
		text-shadow: 0 1px 3px rgba(35, 31, 32, 0.54);
		font-size: 20px;
		@include PBold;
		padding: 10px;
		width: 100%;
		height: 51px;
		box-shadow: 0 4px 27px rgba(118, 165, 240, 0.47);
		border-radius: 25px; }
	&__text {
		color: #fff;
		font-size: 13px;
		@include OSLight;
		text-align: center;
		line-height: 1.3;
		a {
			color: $dark-blue; } } }
