.rel-posts {
	padding: 94px 0 95px;
	@include md {
		padding: 50px 0; }
	&_sm-padding {
		padding: 40px 0 25px; }
	&_articles {
		.slick-dots {
			@include xs {
				opacity: 0;
				visibility: hidden; } } }
	&__inner {
		@include clr;
		margin-bottom: 30px;
		.post-block {
			float: left;
			width: calc(25% - 20px);
			margin-right: 20px;
			&:last-child {
				margin-right: 0; } }
		.slick-slide {
			padding: 10px 10px 20px 10px; }
		.btn-prev, .btn-next {
			background-color: #b8ecf7;
			position: absolute;
			bottom: 0; }
		.btn-prev {
			left: 50px; }
		.btn-next {
			right: 50px; }
		.slick-dots li {
			margin: 5px;
			button {
				width: 10px;
				height: 10px; } }
		@include md {
			max-width: 850px;
			margin: 0 auto 25px; }
		@include sm {
			max-width: 625px;
			margin: 0 auto 25px; }
		@include xs {
			max-width: 300px;
			margin: 0 auto 25px; } }
	&__button {
		text-align: center; }
	&__title {
		font-size: 35px;
		@include md {
			font-size: 25px;
			text-align: center; } } }

.post-block {
	background-color: $almost-white;
	padding: 20px;
	border-radius: 3px;
	box-shadow: 0 4px 16px rgba(0,0,0,.05);
	@include trans;
	margin-top: 19px;
	max-width: 300px;
	&:hover {
		box-shadow: 0 4px 16px rgba(20,193,229,.24);
		.post-block__title {
			color: $cyan; } }
	&__img {
		height: 155px;
		margin-bottom: 14px;
		img {
			max-width: 100%;
			height: 100%;
			display: block;
			object-fit: cover; } }
	&__title {
		font-size: 17px;
		@include trans;
		@include PSemi; }
	&__text {
		font-size: 13px;
		line-height: 1.46;
		margin-bottom: 20px; }
	&__meta {
		padding-top: 20px;
		border-top: 1px solid $gray-border; } }
.meta-tag {
	position: relative;
	padding-left: 21px;
	font-size: 13px;
	line-height: 1;
	@include PReg;
	text-transform: uppercase;
	&__circle {
		display: block;
		width: 11px;
		height: 11px;
		border-radius: 50%;
		position: absolute;
		left: 0;
		top: 0; } }

