.popup-form {
	text-align: left;
	.btn {
		width: 100%;
		margin-bottom: 20px; }
	&__title {
		@include PSemi;
		color: $black;
		&_mb {
			margin-bottom: 20px; } }
	&__text {
		font-size: 13px;
		color: $grey;
		margin-bottom: 28px; }
	&__policy {
		font-size: 11px;
		color: $grey;
		a {
			color: $cyan;
			@include trans;
			&:hover {
				color: #14a8d9; } } }
	&__controls {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		.btn {
			width: 180px;
			margin-bottom: 0; } }
	&__link {
		flex-grow: 1;
		text-align: center;
		@include PBold;
		font-size: 14px;
		color: $cyan;
		padding: 2px 0 0 20px; } }

