.table {
	font-size: 14px;
	box-shadow: 4px 4px 16px 0px rgba(0,0,0,0.05);
	$this: &;
	&__row {
		display: flex;
		&:nth-child(odd) {
			background-color: $little-grey; }
		&:nth-child(even) {
			border-left: 1px solid #edf0f1; } }
	.table__row--first {
		border-radius: 5px 5px 0 0;
		background-color: $cyan;
		#{$this}__col {
			padding: 11px 18px 9px; } }
	&__option {
		@include PBold;
		font-size: 16px;
		color: $white; }
	&__col {
		display: flex;
		align-items: center;
		padding: 19px 19px 8px;
		border-right: 1px solid #edf0f1;
		@include sm {
			padding: 35px 10px; }
		&:nth-child(1) {
			width: 24%;
			@include md {
				width: 25%; }
			@include sm {
				width: 60%; } }
		&:nth-child(2) {
			width: 19%;
			@include md {
				width: 20%; }
			@include sm {
				width: 40%; } }
		&:nth-child(3) {
			width: 48%;
			@include md {
				width: 55%; }
			@include sm {
				display: none; } }
		&:nth-child(4) {
			width: 17%;
			@include md {
				display: none; } } }
	&__price {
		font-size: 16px;
		color: $green;
		@include PBold; }
	&__desc {
		@include OSLight; }
	&__more-link {
		display: none;
		color: $cyan;
		font-size: 12px;
		position: relative;
		padding-right: 15px;
		span {
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			border-radius: 50%;
			width: 11px;
			height: 11px;
			background-color: #9ce3f1; }
		.icon {
			position: absolute;
			font-size: 5px;
			transform: translate(-50%, -50%);
			top: 50%;
			left: 55%;
			fill: #f6f9f9; }
		@include sm {
			display: inline-block; } }
	&__more-mobile {
		display: none; } }

