.faq-section {
	&__inner {
		max-width: 818px; }
	&__title {
		margin-bottom: 12px;
		@include md {
			font-size: 25px;
			text-align: center; } } }
.faq-item {
	padding-left: 37px;
	margin-bottom: 20px;
	@include md {
		padding-left: 0; }
	&__head {
		position: relative;
		width: 780px;
		min-height: 40px;
		background-color: rgba($light-blue, 0.6);
		border-radius: 10px;
		// padding-right: 40px
		// padding-left: 35px
		padding: 9px 40px 9px 35px;
		@include md {
			width: calc(100% - 15px);
			margin-left: 15px;
			padding-left: 54px;
			margin-bottom: 10px; } }
	&__icon {
		position: absolute;
		top: 50%;
		left: -10px;
		transform: translate(-50%, -50%);
		@include md {
			left: 15px; } }

	&__head-text {
		font-size: 16px; }
	&__arrow {
		position: absolute;
		top: 50%;
		margin-top: -3px;
		transform-origin: 50% 50%;
		right: 22px;
		font-size: 0;
		display: block;
		@include trans;
		z-index: 1;
		@include md {
			display: none; }
		svg {
			width: 13px;
			height: 7px;
			fill: $cyan;
			font-size: 7px; } }
	&__content {
		padding-left: 36px;
		padding-right: 15px;
		padding-top: 8px;
		font-size: 14px;
		@include sm {
			padding-left: 0;
			padding-right: 0; } }
	&__close {
		background: transparent;
		min-width: 73px;
		padding: 5px 5px 6px;
		border: 1px solid #d7d7d7;
		border-radius: 15px;
		appearance: none;
		display: inline-block;
		vertical-align: top;
		margin-top: 30px;
		font-size: 14px;
		@include PBold;
		color: #d7d7d7;
		@include md {
			display: none; } }
	&:not(.faq-item--opened) {
		.faq-item__content {
			display: none;
			@include md {
				display: block; } }
		.faq-item__head {
			cursor: pointer;
			@include nosel; } }
	&--opened {
		margin-bottom: 35px; }

	&.is-open {
		.faq-item__arrow {
			transform: rotateX(180deg); } } }

