$icons: map-merge($icons,('heart-green': (X: -107px, Y:-48px, W: 18px, H: 16px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('heart-white': (X: -107px, Y:-74px, W: 18px, H: 16px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('metro': (X: -50px, Y:-102px, W: 16px, H: 11px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('percent-icon': (X: 0px, Y:-57px, W: 35px, H: 35px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user-blue': (X: -45px, Y:-57px, W: 16px, H: 16px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user-cyan': (X: -71px, Y:-57px, W: 16px, H: 16px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user-white': (X: 0px, Y:-102px, W: 16px, H: 16px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('worktime-icon': (X: -26px, Y:-102px, W: 14px, H: 14px, TW: 147px, TH: 118px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

