.reviews {
	@include md {
		text-align: center; }
	&__title {
		margin-bottom: 10px;
		@include md {
			font-size: 25px;
			margin-bottom: 20px; } }
	&__list {
		margin-bottom: 27px;
		@include md {
			margin-bottom: 20px; } }
	&__btn {
		text-align: center;
		a {
			margin: 0 10px;
			@include md {
				margin: 5px; } } } }

.review {
	@include md {
		text-align: left; }
	&__row {
		&:not(:last-child) {
			padding-bottom: 24px;
			border-bottom: 1px solid #edf0f1;
			margin-bottom: 10px;
			@include md {
				padding-bottom: 30px;
				margin-bottom: 28px; } }
		@include clr; }
	&__state {
		float: left;
		width: 173px;
		padding-top: 19px;
		@include md {
			width: 100%;
			float: none;
			padding-top: 0;
			@include clr;
			margin-bottom: 0; } }
	&__body {
		overflow: hidden;
		padding-top: 15px;
		@include md {
			overflow: visible;
			padding-top: 10px; } }
	&__top {
		margin-bottom: 3px; }
	&__title {
		color: #cacdce;
		letter-spacing: -.5px;
		@include OSReg;
		a {
			color: $cyan; } }
	&__text {
		margin-bottom: 5px;
		p {
			font-size: 14px;
			line-height: 1.72;
			color: $black; } }
	&__inline {
		margin: 0 -9px;
		@include md {
			margin: 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 9px;
		@include md {
			padding: 0;
			display: block; } }
	&__author, &__date {
		font-size: 15px;
		color: #cacdce;
		line-height: 1.2;
		@include OSSemi; }
	&__wrapper {
		@include md {
			float: left; } }
	&__status {
		@include md {
			float: right; } } }

.review-status {
	$this: &;
	@include md {
		text-align: right; }
	&_like {
		#{$this}__title {
			color: #77d262; } }
	&_dis {
		#{$this}__title {
			color: $pink; } }
	&__title {
		@include PBlack;
		font-size: 16px;
		line-height: 1;
		text-transform: uppercase;
		margin-bottom: 48px;
		@include md {
			margin-bottom: 0; } }
	&__icon {
		width: 17px;
		height: 17px;
		margin-bottom: 5px;
		.icon {
			font-size: 15px; }
		@include md {
			display: inline-block; } } }




