.costs {
	&__title {
		margin-bottom: 4px; }
	&__btn {
		text-align: center; }
	&__list {
		margin-bottom: 18px;
		&_mb {
			margin-bottom: 27px; } } }
.costs-table {
	min-width: 100vw;
	overflow: auto;
	&__row {
		display: flex;
		background-color: #fff;
		padding-left: 29px;
		min-height: 40px;
		@include md {
			min-height: 30px; }
		&:nth-child(even) {
			background-color: $little-grey; } }
	&__col {
		display: flex;
		&_wide {
			width: 37%; }
		&_sm {
			padding-left: 3px;
			width: 15%; }
		&_mid {
			width: 26%; }
		&_last {
			flex: 1;
			padding-left: 4px;
			justify-content: flex-end;
			padding-right: 32px;
			@include md {
				display: none; } }
		&.is-hidden {
			display: none; }
		&_long {
			width: 53%; }
		&_space-md {
			width: 15%;
			padding-left: 7px;
			@include md {
				width: 22%; } }
		&_indent_md {
			width: 25%;
			min-width: 25%; }
		&_indent_sm {
			width: 21%;
			min-width: 21%;
			@include md {
				width: 24%;
				min-width: 24%; }
			&_last {
				flex: 1; } } }
	&__title {
		font-size: 16px;
		@include OSReg;
		line-height: 1;
		padding-top: 9px;
		color: $black;
		@include md {
			font-size: 12px; }
		&_head {
			padding-top: 10px;
			@include PBold; }
		&_secondary {
			@include PBold;
			padding-top: 0;
			padding-left: 6px;
			span {
				@include PReg;
				padding-top: 2px;
				font-size: 14px;
				display: block; } } }
	&__price {
		font-size: 16px;
		color: $green;
		padding-top: 6px;
		letter-spacing: .3px;
		white-space: nowrap;
		@include OSBold;
		@include md {
			font-size: 12px; }
		&_mod {
			color: $black; } }
	&__s-link {
		color: #46cae8;
		font-size: 14px;
		@include OSReg;
		padding-top: 8px; }
	&__enroll {
		color: $cyan;
		font-size: 16px;
		@include PBold;
		position: relative;
		padding-right: 31px;
		padding-top: 8px;
		span {
			position: absolute;
			right: 0;
			top: 7px;
			border-radius: 50%;
			width: 22px;
			height: 22px;
			background-color: #9ce3f1; }
		.icon {
			position: absolute;
			font-size: 8px;
			margin-top: -4px;
			margin-left: -2px;
			top: 50%;
			left: 50%;
			fill: #f6f9f9; } }
	&__item {
		padding: 0 14px;
		&:not(:last-child) {
			margin-right: 3px; } } }
