.button {
	color: #fff;
	font-size: 15px;
	border-radius: 30px;
	padding: 12px 21px 11px;
	@include PSemi;
	display: inline-block;
	@include trans;
	&_cyan {
		background-color: $cyan; }
	&_hero {
		padding: 12px 32px 8px;
		border: 1px solid $cyan;
		color: $cyan;
		letter-spacing: 0.5px;
		@include PBold;
		&:hover {
			background-color: $cyan;
			color: #fff; }
		&_sm {
			padding: 12px 22px 8px; } }
	&_sm {
		padding: 10px;
		min-width: 145px; }

	&_border-white {
		background-color: transparent;
		border: 1px solid #fff;
		color: #fff;
		padding: 11px 21px 10px;
		&:hover {
			background-color: #fff;
			color: $cyan; } }
	&_primary {
		border: 1px solid $cyan;
		@include PBold;
		padding: 18px 73px;
		font-size: 20px;
		@include sm {
			width: 100%; } }
	&_border {
		background-color: transparent;
		border: 1px solid $cyan;
		color: $cyan;
		&:hover {
			background-color: $cyan;
			color: #fff; } }
	&_green {} }


.btn {
	padding: 10px 15px 11px;
	border-radius: 20px;
	color: $white;
	font-size: 16px;
	border: none;
	display: inline-block;
	vertical-align: top;
	appearance: none;
	@include PBold;
	text-align: center;
	@include trans;
	&--w-240 {
		min-width: 240px; }
	&--pink {
		background-color: $pink; }
	&--green {
		background-color: $green;
		font-size: 14px;
		padding: 11px 15px 12px;
		&:hover {
			background-color: #5ad73c; } }
	&--green-border {
		background-color: #fff;
		border: 1px solid $green;
		color: $green;
		font-size: 14px;
		padding: 11px 15px 12px;
		&:hover {
			background-color: $green;
			color: #fff; } }
	&--100 {
		width: 100%;
		padding: 16px 0 14px;
		border-radius: 25px; }
	&--white {
		color: $cyan;
		background-color: $white; }
	&--sm {
		min-width: 90px; }
	&--doctor {
		display: none;
		@include r(720) {
			width: 100%;
			display: block; } }
	&--green-wide {
		font-size: 16px;
		padding: 10px 15px 9px;
		background-color: $green;
		min-width: 110px; } }
.btnNext, .btnPrev {
	width: 28px;
	height: 28px;
	background-color: $little-grey;
	cursor: pointer;
	text-align: center;
	border-radius: 50%;
	position: absolute;
	z-index: 1;
	i {
		@include vcenter; }
	svg {
		width: 7px;
		height: 12px;
		fill: #14c1e5; }
	&.slick-disabled {
		cursor: default;
		@include nosel;
		user-select: none; } }
.btn-prev,
.btn-next {
	border: 0;
	border-radius: 50%;
	width: 22px;
	height: 22px;
	display: inline-block;
	vertical-align: middle;
	font-size: 9px;
	background: #b8ecf7;
	z-index: 1;
	svg {
		position: absolute;
		top: 50%;
		left: 50%;
		fill: $white; } }
.more-link {
	color: $cyan;
	@include OSLight;
	position: relative;
	display: inline-block;
	// &:before
	// 	position: absolute
	// 	top: 0
	// 	left: 0
	// 	display: block
	// 	content: ''
	// 	background-image: url("../img/arrow-sm.png")
	// 	background-repeat: no-repeat
	// 	background-size: cover
	// 	background-position: center
	span {
		padding-left: 20px;
		display: inline-block; }
	&__icon {
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background-color: #9ce3f1;
		display: block;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		.icon {
			display: block;
			font-size: 7px;
			fill: #fff;
			@include vcenter;
			margin-left: 1px; } }
	&--sm {
		font-size: 14px;
		// &:before
		// 	top: 2px
		// 	height: 16px
 } }		// 	width: 16px
.slick-dots {
	text-align: center;
	font-size: 0;
	li {
		display: inline-block;
		vertical-align: top;
		margin: 5px 10px;
		button {
			appearance: none;
			border: none;
			width: 7px;
			height: 7px;
			background-color: #dcdddd;
			border-radius: 50%;
			position: relative;
			font-size: 0;
			@include trans;
			@include md {
				width: 10px;
				height: 10px; }
			&:before {
				content: '';
				position: absolute;
				top: -3px;
				bottom: -3px;
				left: -3px;
				right: -3px; } }
		&.slick-active {
			button {
				background-color: $cyan; } } } }

.sales-link {
	font-size: 14px;
	color: $green;
	@include PBold;
	line-height: 25px;
	span {
		text-align: center;
		display: inline-block;
		@include PExtra;
		font-size: 13px;
		background-color: $green;
		color: $white;
		height: 22px;
		width: 22px;
		border-radius: 50%;
		margin-right: 4px; } }

.forget-link {
	color: #b2ecff;
	font-size: 14px; }
.btn-yes, .btn-no {
	min-width: 60px;
	padding: 10px 10px;
	border-radius: 20px;
	display: inline-block;
	vertical-align: top;
	font-size: 14px;
	text-align: center;
	@include PBold;
	cursor: pointer;
	appearance: none;
	@include trans;
	@include nosel; }
.btn-yes {
	background-color: #cbf2c0;
	border: 1px solid #77d262;
	margin-right: 10px;
	color: #77d262;

	&.is-active {
		background-color: #77d262;
		color: $white; } }
.btn-no {
	background-color: #f9dce9;
	border: 1px solid #ff016e;
	color: #ff016e;
	&.is-active {
		background-color: #ff016e;
		color: $white; } }
