@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }

body {
	@include OSReg;
	@include smooth-font;
	font-size: 16px;
	color: $black;
	&.is-hidden {
		height: 100%;
		width: 100%;
		position: fixed;
		overflow: hidden; }
	&.is-filters-open {
		.header__logo {
			display: none; }
		.header__mob-filters-close {
			display: block; } } }
