a {
	text-decoration: none;
	color: $black; }

h1, .title-lg {
	font-size: 50px;
	@include PSemi;
	@include md {
		font-size: 25px; } }

h2, .title-md {
	font-size: 35px;
	@include PSemi;
	@include md {
		font-size: 25px; } }

h3, .h3, .title-sm {
	font-size: 30px;
	@include PSemi;
	@include md {
		font-size: 20px; } }

h4, .h4, .title-xs {
	font-size: 20px;
	@include PSemi;
	@include md {
		font-size: 14px; } }

.container {
	max-width: 1260px;
	margin: 0 auto;
	padding: 0 40px;
	@include clr;
	@include r(399) {
		padding: 0 20px; } }
.col {
	float: left;
	&_lg-left {
		width: percentage(900/1180);
		padding-right: 20px;
		@include md {
			padding-right: 0; } }
	&_lg-right {
		width: percentage(900/1180);
		padding-left: 20px;
		@include md {
			padding-left: 0; } }
	&_sm {
		width: percentage(280/1180); }
	@include md {
		float: none;
		width: 100%; } }

.text {
	line-height: 1.5;
	&_gutter {
		margin-bottom: 34px; }
	@include sm {
		font-size: 14px; } }
.text-cyan {
	color: $cyan; }
.text-green {
	color: $green; }
.text-bold {
	@include OSBold; }
.text-crossed {
	text-decoration: line-through; }

.list {
	line-height: 24px;
	margin: 26px 0 25px;
	@include sm {
		font-size: 14px; }
	&__item {
		position: relative;
		padding-left: 21px;
		&:before {
			font-size: 30px;
			display: block;
			position: absolute;
			content: '';
			color: $cyan;
			top: 0;
			left: 3px; } } }
.map-link {
	display: block;
	border-radius: 5px;
	overflow: hidden;
	position: relative;
	p {
		@include PSemi;
		color: $white;
		text-align: center;
		width: 100%;
		background-color: $cyan;
		padding: 9px 5px 8px;
		position: absolute;
		left: 0;
		bottom: 0; }
	&--search {
		height: 264px;
		width: 100%;
		max-width: 280px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
.out {
	background-color: #fff;
	// width: 100vw
	overflow: hidden;
	&--bg-gray {
		background-color: $almost-white; }
	&--links-popup {
		background: rgba(black, 0.5);
		.popup {
			display: block;
			position: static;
			width: auto;
			height: auto;
			&__content {
				position: relative;
				left: 0;
				top: 0;
				transform: none;
				margin: 50px auto 50px; } } }
	&.is-stiky {
		padding-top: 71px;
		@include md {
			padding-top: 45px; }
		@include xs {
			padding-top: 40px; } } }
.article {
	color: $black;
	@include OSReg;
	a {
		color: $cyan; }
	&--sm {
		font-size: 14px;
		@include md {
			font-size: 13px; } }
	&--mb-95 {
		margin-bottom: 95px;
		@include md {
			margin-bottom: 60px; } } }
.centered {
	text-align: center; }
.short-text {
	padding-bottom: 36px; }
.bg-white {
	background-color: $white; }

.show-md {
	display: none;
	@include md {
		display: block; } }
.hide-md {
	display: block;
	@include md {
		display: none; } }

.show-sm {
	display: none;
	@include sm {
		display: block; } }
.hide-sm {
	display: block;
	@include sm {
		display: none; } }
