.section-double-slider {
	background: $white;
	padding-top: 46px;
	@include md {
		padding-top: 0;
		background: none; }
	&__title {
		margin-bottom: 30px;
		@include md {
			text-align: center;
			font-size: 25px;
			margin-bottom: 20px; } }
	&__inner {
		padding: 0 44px;
		position: relative;
		@include md {
			padding: 0; } }

	.btnPrev, .btnNext {
		bottom: auto;
		top: 50%;
		transform: translateY(-50%); }
	.btnPrev {
		left: -44px; }
	.btnNext {
		right: -44px; }
	.rate-wrapper {
		max-width: 252px; } }
