.popup {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 1002;
	overflow-y: auto;
	background: rgba(#000, 0.5);
	//+m
	//	overflow-y: scroll
	&__overlay {
		height: 100%;
		position: absolute;
		width: 100%;
		//+m
 }		//	display: none
	&__content {
		padding: 30px 20px 45px;
		border-radius: 5px;
		left: 50%;
		top: 15%;
		position: absolute;
		transform: translateX(-50%);
		width: 400px;
		background-color: white;
		text-align: center;
		margin-bottom: 2vh;
		@include sm {
			width: 320px; }
		//+m
		//	overflow-y: scroll
		//	width: 100%
		//	height: 100%
		//	border: none
		//	position: relative
		//	top: 0
		//	left: 0
		//	transform: translate(0, 0)
		//	padding: 60px 20px 25px
 }		//	margin-bottom: 0
	&__close {
		position: absolute;
		width: 16px;
		height: 16px;
		top: 19px;
		right: 19px;
		cursor: pointer;
		@include trans;
		@include sm {
			height: 11px;
			width: 11px;
			top: 10px;
			right: 10px; }
		.icon {
			fill: $grey;
			width: 100%;
			height: 100%; }
		&:before {
			content: '';
			@include vcenter; } }
	&_cities {
		.popup__content {
			padding: 50px;
			text-align: left;
			width: 1000px;
			@include md {
				width: 850px; }
			@include r(850) {
				width: 600px; }
			@include sm {
				width: 320px;
				padding: 20px; } } }
	&_appoint {
		.popup__content {
			width: 840px;
			padding: 0;
			border-radius: 3px;
			@include r(850) {
				width: 600px; }
			@include sm {
				width: 320px; } } }
	&_register {
		.popup__content {
			width: 780px;
			padding: 0;
			border-radius: 3px;
			@include r(850) {
				width: 600px; }
			@include sm {
				width: 320px; } } }
	&--map {
		.popup__content {
			width: 1000px;
			padding: 0;
			border-radius: 5px;
			@include r(1080) {
				width: 760px; }
			@include r(830) {
				width: 100%; } }
		.gmap {
			height: 647px;
			border-radius: 5px;
			@include r(1080) {
				height: 500px; } }
		.popup__close {
			right: -26px;
			top: 0;
			@include r(830) {
				right: 11px;
				top: -31px; } } }
	&--login {
		.popup__content {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 50px 80px;
			width: 440px;
			background-image: linear-gradient(to top, #14c1e5 0%, #3bd7f7 100%);
			@include sm {
				width: 320px;
				padding: 20px; } }
		.logo {
			width: 169px;
			height: 57px;
			margin-bottom: 34px; }
		.form {
			margin-bottom: 21px; }
		.have-account {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 100%);
			@include OSSemi;
			font-size: 14px;
			width: 320px;
			border-radius: 0 0 50px 50px;
			padding: 11px  0 14px;
			background-color: #d0f2fa;
			a {
				color: #14a8d9; } }
		.popup__close {
			opacity: 0.7;
			.icon {
				fill: $white; } } }
	&_reminder {
		.popup__content {
			width: 450px;
			padding-bottom: 40px; } } }
.baloon {
	width: 220px;
	border-radius: 5px;
	background: $white;
	text-align: center;
	padding-top: 16px;
	padding-bottom: 20px;
	position: relative;

	&:after {
		position: absolute;
		content: '';
		width: 18px;
		height: 9px;
		bottom: -9px;
		left: 50%;
		transform: translateX(-50%);
		@include arr(18, 9, $white, b); }
	&__content {}
	&__title, &__price {
		font-size: 14px;
		color: $cyan;
		@include PExtra; }
	&__title {
		margin-bottom: 10px; }
	&__text {
		@include PSemi;
		font-size: 14px;
		margin-bottom: 2px; }
	.rate-stars {
		margin-bottom: 18px; } }

.popup-cols {
	display: flex;
	&__left {
		background: linear-gradient(to bottom, #3bd7f7, $cyan);
		width: percentage(310/840);
		padding: 50px;
		position: relative;
		@include r(850) {
			display: none; } }
	&__right {
		width: percentage(530/840);
		background: $white;
		padding: 50px;
		@include r(850) {
			width: 100%; }
		@include sm {
			padding: 30px 20px 20px; } }
	&__logo {
		width: 170px;
		margin: 0 auto 16px;
		img {
			max-width: 100%;
			height: auto; } }
	&__name {
		font-size: 17px;
		@include PBold;
		color: #13c1e5;
		text-transform: uppercase; }
	&__image {
		opacity: .5;
		position: absolute;
		bottom: 50px;
		left: 50%;
		transform: translateX(-50%); } }


.reminder {
	padding-top: 17px;
	&__title {
		font-size: 20px;
		@include PSemi;
		margin-bottom: 19px; }
	&__btns {
		margin: 0 -10px;
		font-size: 0; }
	&__btn {
		display: inline-block;
		vertical-align: top;
		padding: 0 10px; } }

