.services {
	background-color: $white;
	padding: 96px 20px 50px;
	@include md {
		padding: 50px 0;
		background: transparent; }
	$this: &;
	&__top {
		margin-bottom: 52px;
		@include md {
			margin-bottom: 24px; } }
	&__title {
		font-size: 35px;
		text-align: center;
		@include PSemi;
		@include md {
			font-size: 25px; } }
	&__text {
		text-align: center; }
	&__in {
		max-width: 1180px;
		margin: 0 auto; }
	&__row {
		display: flex;
		justify-content: space-between;
		margin-bottom: 48px;
		@include md {
			display: block;
			margin-bottom: 0; } }
	&__item-title {
		font-size: 20px;
		position: relative;
		@include PBold;
		@include md {
			background-color: #edf9fb;
			border: 1px solid #b8ecf7;
			border-radius: 25px;
			color: $cyan;
			padding: 15px 40px 12px 20px;
			margin-bottom: 0px;
			font-size: 18px;
			@include PReg; }
		& > svg {
			display: none;
			position: absolute;
			top: 50%;
			right: 18px;
			// transform: translateY(-50%)
			margin-top: -10px;
			transform-origin: 50% 50%;
			stroke: $cyan;
			width: 14px;
			transform: rotateZ(-90deg);
			@include trans;
			@include md {
				display: block; } } }
	&__item {
		margin-right: 20px;
		width: 333px;
		@include md {
			margin: 0 auto;
			margin-bottom: 10px;
			max-width: 320px;
			width: 100%; }
		&:last-of-type {
			margin-right: 0;
			@include md {
				margin-right: auto; } }
		@include md {
			&.is-open {
				margin-bottom: 25px;
				.services__item-title {
					svg {
						transform: rotateZ(0deg); } } } } }
	&__list {
		margin-top: 8px;
		&.is-active {
			.more-link__icon {
				transform: translateY(-50%) rotate(-90deg); } }
		li {
			&:last-of-type {
				@include md {
					display: none; } } }
		@include md {
			display: none; } }

	&__list-item {
		display: block;
		position: relative;
		margin-bottom: 7px;
		&:before {
			@include pseudo;
			top: auto;
			bottom: 5px;
			border-bottom: 1px dotted #b8b8b8;
			width: 100%; }
		&:last-of-type {
			margin-bottom: 0;
			@include md {
				display: none; } }
		&.is-hidden {
			display: none;
			@include md {
				display: block; } } }
	&__name {
		display: inline-block;
		font-size: 17px;
		position: relative;
		padding-right: 3px;
		@include OSReg; }
	&__name,
	&__value {
		background-color: $white;
		z-index: 5;
		@include md {
			font-size: 14px; } }
	&__value {
		bottom: 0;
		color: $bright-blue;
		font-size: 15px;
		font-weight: 600;
		padding-left: 3px;
		position: absolute;
		right: 0;
		@include OSSemi; }
	&__link {
		margin-top: 1px; }
	.more-link {
		font-size: 17px;
		&__icon {
			height: 21px;
			transition: transform linear 0.3s;
			width: 21px; }
		span {
			padding-left: 30px; } }
	&_mod {
		padding: 0;
		#{$this}__name {
			font-size: 14px;
			@include OSLight; }
		#{$this}__list-item {
			&:not(:last-child) {
				margin-bottom: 8px; } } } }
