.main {
	.hero {
		padding-top: 200px;
		@include sm {
			padding-top: 109px; } }
	.section-video {
		padding: 125px 10px 100px;
		@include sm {
			padding: 47px 10px 42px; }
		.video {
			&__title {
				@include sm {
					margin-bottom: 0; } }
			&__list {
				@include sm;
				margin: 0 auto;
				.list {
					@include sm {
						margin-top: 14px;
						margin-bottom: 1px; } } }
			&__player {
 } } }				//max-width: 323px
	.faq-section {
		padding: 100px 10px;
		@include sm {
			padding: 66px 23px; }
		&__title {
			text-align: center;
			@include sm {
				margin-bottom: 21px; } }
		&__inner {
			margin: 0 auto; } }
	.faq-item {
		&__content {
			@include sm {
				padding: 0;
				margin-top: 15px;
				text-indent: 13px;
				line-height: 1.7em; } }
		&__head {
			@include sm {
				margin-bottom: 8px; } }
		&--opened {
			@include sm {
				margin-bottom: 29px; } } }
	.badge-slider {
		padding-top: 90px;
		@include md {
			padding-top: 0; } }
	.container {
		@include sm {
			padding: 0 20px; } }
	.app {
		@include sm {
			padding: 20px 0 40px; }
		&__title {
			@include sm {
				margin-bottom: 14px; } }
		&__in {
			@include sm {
				h4 {
					margin-bottom: 32px; } } } }
	.badge-slider {
		@include sm {
			padding: 0 20px; }
		&__titlebox {
			@include md {
				background-color: transparent;
				padding-top: 67px;
				padding-bottom: 0;
				min-height: auto;
				width: 100%; } }
		&__title {
			@include md {
				color: $black;
				max-width: 100%;
				margin-bottom: 0;
				text-align: center; } }
		@include md {
			display: block;
			&__left {
				display: block;
				width: 100%; }
			.sale-block {
				margin: auto;
				width: 300px;
				&__icon {
					display: block;
					right: 18px; }
				&__title {
					max-width: 260px; } } } } }
