.banner {
	background: rgba(59,216,247,1);
	background: -moz-linear-gradient(top, rgba(59,216,247,1) 0%, rgba(20,193,229,1) 100%);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(59,216,247,1)), color-stop(100%, rgba(20,193,229,1)));
	background: -webkit-linear-gradient(top, rgba(59,216,247,1) 0%, rgba(20,193,229,1) 100%);
	background: -o-linear-gradient(top, rgba(59,216,247,1) 0%, rgba(20,193,229,1) 100%);
	background: -ms-linear-gradient(top, rgba(59,216,247,1) 0%, rgba(20,193,229,1) 100%);
	background: linear-gradient(to bottom, rgba(59,216,247,1) 0%, rgba(20,193,229,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3bd8f7', endColorstr='#14c1e5', GradientType=0 );
	padding: 140px 0 135px;
	@include md {
		padding: 63px 0 75px; }
	&__in {
		color: $white;
		max-width: 896px;
		margin: 0 auto; }
	&__title {
		font-size: 35px;
		margin: 0 auto 60px;
		max-width: 650px;
		text-align: center;
		text-shadow: 0 3px 0 rgba(72, 61, 224, 0.24);
		@include PSemi;
		@include md {
			font-size: 24px; }
		@include sm {
			margin-bottom: 57px; } }
	&__grid {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		@include md {
			flex-direction: column;
			align-items: center; } }
	&__item {
		text-align: center;
		padding: 0 10px;
		width: 240px;
		@include md {
			width: 290px;
			display: flex;
			text-align: left;
			&:not(:last-child) {
				margin-bottom: 44px; } }
		@include sm {
			padding: 0;
			&:not(:last-child) {
				margin-bottom: 36px; } } }
	&__item-image {
		height: 145px;
		margin-bottom: 19px;
		position: relative;
		@include md {
			height: 75px;
			margin-right: 40px;
			min-width: 98px; }
		@include r(375) {
			margin-right: 20px; } }
	&__item-title {
		font-size: 37px;
		line-height: 1em;
		text-shadow: 0 3px 0 rgba(72, 61, 224, 0.24);
		margin-bottom: 8px;
		white-space: nowrap;
		@include PExtra;
		@include md {
			font-size: 25px; }
		@include sm {
			margin-bottom: 2px; } }
	&__item-content {
		@include sm {
			padding-top: 15px; } }
	&__item-txt {
		font-size: 18px;
		@include PSemi;
		@include md {
			font-size: 14px; } }
	&__illustration {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		z-index: 10;
		&_clinic {
			height: 97px;
			width: 142px;
			@include md {
				height: 58px;
				width: 84px; } }
		&_doctor {
			width: 115px;
			height: 101px;
			@include md {
				height: 60px;
				width: 68px; } }
		&_feedback {
			width: 107px;
			height: 82px;
			top: 15px;
			@include md {
				height: 49px;
				width: 63px; } } }
	&__cloud {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 168px;
		@include md {
			width: 98px; } } }

.image-group {
	position: relative;
	z-index: 10;
	width: 135px;
	height: 110px;
	@include md {
		width: 100px;
		height: 105px; }
	&__head {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		&_clinic {
			height: 74px;
			width: 107px;
			margin-left: -5px;
			@include md {
				margin-left: -2px;
				width: 84px;
				height: 90px; } }
		&_doctor {
			width: 102px;
			height: 90px;
			position: relative;
			top: -4px;
			left: -3px;
			@include md {
				width: 68px;
				height: 60px;
				left: 0;
				top: 23px; } } }

	&__cloud {
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: -1;
		@include md {
			width: 100%; } } }

.search-banner {
	display: block;
	text-align: center;
	padding-top: 33px;
	padding-bottom: 32px;
	background-image: linear-gradient(-30deg, #14c1e5 0%, #3bd7f7 100%);
	@include md {
		@include clr;
		padding-top: 21px;
		padding-bottom: 35px;
		display: flex;
		justify-content: center; }
	&__wrapper {
		display: block;
		@include sm {
			margin-right: -40px;
			margin-left: -57px;
			@include clr; }
		@include r(400) {
			margin-left: -10px;
			margin-right: -20px; } }
	&__image {
		display: inline-block;
		vertical-align: middle;
		left: -27px;
		@include md {
			left: 0;
			float: left;
			margin-right: 10px; } }
	&__title {
		display: inline-block;
		vertical-align: middle;
		font-size: 35px;
		margin-left: 44px;
		padding-top: 11px;
		letter-spacing: -.9px;
		@include md {
			font-size: 23px;
			text-align: center;
			width: 210px;
			float: left;
			margin-left: 0;
			padding-top: 33px;
			letter-spacing: .7px; }
		@include r(400) {
			font-size: 20px;
			padding-top: 40px;
			width: 160px; } } }




