.articles-container {
	margin-bottom: 100px;
	@include sm {
		margin-bottom: 20px;
		border-bottom: 1px solid $gray-border;
		&:last-child {
			border-bottom: none; }
		.articles-preview__image {
			display: none; }
		.articles-preview__info {
			padding-left: 0; }
		.meta-tag {
			display: none; }
		.more-link {
			display: none; }
		.articles-preview {
			padding: 0; }
		.articles-preview__textual {
			max-height: 67px;
			overflow: hidden; } }
	&__header {
		position: relative;
		@include sm {
			display: none; }
		&:before {
			@include pseudo;
			bottom: 0;
			border-bottom: 1px solid $really-grey;
			top: auto;
			opacity: 0.5;
			width: 100%; }
		.more-link {
			position: absolute;
			bottom: 15px;
			right: 0; } }
	&__title {
		display: inline-block;
		font-size: 30px;
		padding-bottom: 3px;
		position: relative;
		@include PSemi;
		&:before {
			@include pseudo;
			top: auto;
			bottom: -0.5px;
			background-color: $cyan;
			height: 3px;
			width: 100%; } }
	&__body {
		display: flex;
		flex-direction: row; }
	.item {
		&__title {
			font-size: 14px; } }
	.popular-articles {
		padding: 5px 0;
		min-width: 280px; }
	.articles-preview__textual {
		max-height: 135px;
		overflow: hidden; }
	&--month {
		margin-bottom: 72px;
		.articles-preview {
			padding: 20px 0;
			&__image {
				height: 175px; }
			&__textual {
				max-height: 65px;
				overflow: hidden; } } } }
