.hospital-filters {
	background-color: $white;
	border-radius: 5px;
	box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
	padding: 30px;
	@include md {
		display: none;
		&.is-active {
			display: block;
			position: absolute;
			top: 60px;
			left: 0;
			z-index: 1000;
			width: 100%; } }
	&__block {
		margin-bottom: 27px;
		&:last-of-type {
			margin-bottom: 0; } }
	&__title {
		font-size: 20px;
		margin-bottom: 5px;
		@include PBold;
		@include lg {
			font-size: 16px; } }
	&__mob-btns {
		margin-top: 30px;
		display: none;
		@include md {
			display: block; }
		.btn {
			padding: 9px 15px 8px;
			margin-bottom: 20px; } }
	.range-inputs {
		input {
			width: 44%;
			max-width: 100px; } }
	.custom-select {
		margin-bottom: 10px;
		&:last-of-type {
			margin-bottom: 0; }
		&__field {
			border: 1px solid $really-grey;
			border-radius: 20px;
			font-size: 14px;
			padding: 14px 28px 10px;
			margin: 0; }
		&__text {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis; }
		&__dropdown {
			border: 1px solid $really-grey; }
		.icon {
			fill: $grey;
			transition: all .3s;
			height: 12px;
			width: 18px;
			stroke: #b8b8b8;
			position: absolute;
			top: 50%;
			left: 10px;
			-ms-transform: translateY(-50%);
			transform: translateY(-50%);
			&-arrow-down {
				left: auto;
				right: 10px; } }
		&.is-open {
			.custom-select {
				&__field {
					border-radius: 20px 20px 0 0;
					border-bottom: none; }
				&__dropdown {
					border-radius: 0 0 20px 20px; } } } } }
