.pagination {
	@include md {
		text-align: center; }
	&__text {
		color: $cyan;
		display: inline-block;
		vertical-align: middle;
		@include PBold;
		@include md {
			display: none; } }
	&__link-list {
		display: inline-block;
		font-size: 0;
		margin-left: 10px;
		vertical-align: middle;
		@include md {
			margin-left: 0; } }
	&__item {
		color: $cyan;
		font-size: 16px;
		display: inline-block;
		min-height: 30px;
		vertical-align: middle;
		min-width: 30px;
		&:not(:last-child) {
			margin-right: 17px; } }
	&__link {
		background-color: $almost-white;
		border-radius: 4px;
		color: $cyan;
		display: block;
		height: 100%;
		text-align: center;
		padding: 5px 0;
		width: 100%;
		min-height: 30px;
		&:hover,
		&.is-active {
			background-color: $cyan;
			color: $white; }
		@include md {
			@include PBold;
			padding: 7px 0 3px; } }
	&__next, &__prev {
		position: relative;
		display: block;
		min-height: 30px;
		min-width: 30px;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			margin-top: -4px;
			height: 7px;
			width: 7px;
			border-left: 2px solid $cyan;
			border-bottom: 2px solid $cyan; } }

	&__next {
		&:before {
			left: 0;
			transform: rotate(-135deg); } }
	&__prev {
		margin-right: 19px;
		&:before {
			right: 0;
			transform: rotate(45deg); } }
	&__more {
		@include md {
			display: none; } }
	&__navigation {
		display: none;
		vertical-align: top;
		@include md {
			display: inline-block; } }

	span.pagination__link {
		background-color: transparent;
		&:hover {
			color: $cyan; } } }
