.top-info {
	margin-bottom: 18px;
	@include md {
		text-align: center; }
	&__title {
		margin-bottom: 3px; }
	&__text {
		line-height: 1.3;
		max-width: 850px;
		@include md {
			font-size: 14px;
			line-height: 1.7; } }
	&__map {
		.map-link {
			border: 1px solid transparent;
			height: 94px;
			@include trans;
			&:hover {
				border-color: $cyan; }
			p {
				padding: 4px 5px 1px; } } } }
