.hero {
	background-image: linear-gradient(-225deg, #4d56d3 0%, #23bbd3 100%);
	padding: 160px 0 47px;
	text-align: center;
	&__title {
		color: $white;
		margin-bottom: 5px; }
	&__subtitle {
		@include PLight;
		font-size: 25px;
		color: $white;
		margin-bottom: 21px;
		@include md {
			font-size: 20px; }
		@include sm {
			margin-bottom: 11px; } }
	&__form {
		margin-bottom: 107px;
		@include sm {
			max-width: 300px;
			margin: 0 auto 47px; }
		.form {
			&__field {
				text-align: left;
				margin-right: 20px;
				margin-top: 20px;
				@include sm {
					margin-top: 15px; } }
			.button {
				margin-top: 20px;
				position: relative;
				top: -6px;
				@include r(932) {
					top: 0; }
				@include sm {
					margin-top: 15px; } } }
		@include sm {
			margin-bottom: 50px; } }
	&__items {
		color: $white;
		text-align: left;
		display: flex;
		justify-content: space-between;
		@include md {
			flex-wrap: wrap;
			justify-content: center;
			text-align: center; }
		@include sm {
			flex-direction: column;
			align-items: center; } }
	&__item {
		&:not(:last-child) {
			margin-right: 30px; }
		@include md {
			&:not(:last-child) {
				margin-bottom: 27px; }
			&:nth-child(2) {
				margin-right: 0; } }
		@include sm {
			&:not(:last-child) {
				margin-right: 0; } }
		.item {
			max-width: 310px;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			@include md {
				align-items: center; }
			@include sm {
				width: 100%; }
			&__icon {
				margin-left: -15px;
				margin-bottom: 6px;
				height: 74px;
				width: 74px;
				border-radius: 50%;
				background-color: $cyan;
				display: flex;
				justify-content: center;
				align-items: center;
				@include md {
					margin-left: 0; }
				@include sm {
					height: 58px;
					width: 58px;
					margin-bottom: 9px; }
				.icon {
					fill: $white;
					width: 70%;
					height: 70%; } }
			&__title {
				margin-bottom: 2px;
				@include md {
					font-size: 18px; }
				@include sm {
					margin-bottom: 0; } }
			&__desc {
				font-size: 14px;
				line-height: 1.25; } } }
	.custom-select__field {
		@include sm {
			padding: 18px 28px 14px; } }
	.button_primary {
		@include sm {
			padding: 12px 20px; } } }
