.service-page {
	.rel-posts {
		padding: 60px 0 100px;
		@include sm {
		  padding: 46px 0 50px; } }
	.section-sales {
		background-color: $white;
		padding: 100px 0;
		&__title {
			@include sm {
				font-size: 25px;
				margin-bottom: 5px; } }
		&__after-head-text {
			@include sm {
				font-size: 14px;
				text-align: center;
				margin-bottom: 15px; } }
		.sale-block__map {
			.map-link {
				@include md {
					display: block; } } }
		@include sm {
			padding: 43px 0 34px; } }
	&__popular-links {
		margin-top: 30px;
		margin-bottom: 25px;
		@include md {
			display: none; } }
	.map-link {
		@include md {
			display: none; } }
	.breadcrumbs {
		@include sm {
			margin-bottom: 24px; } }
	.top-info__text {
		@include sm {
			line-height: 1.7; } }
	.map-link--search {
		@include sm {
			max-width: 320px; } }
	.sale-block__btn {
		@include sm {
			width: 100%; } }
	.doctors-list {
		&__title {
			margin-bottom: 18px; }
		@include sm {
			padding: 51px 0; } } }
